import React from 'react';
import data from '../Data/L1Data.json';
import PlayerRow from '../Helpers/PlayerHelper';
import VjezbeRow from '../Helpers/VjezbeHelper';
import Footer from '../Body/MainFooter';

// Bootstrap
import { Row, Col, Table } from 'react-bootstrap';

// Other
import '../App.scss';

function L1() {
	return (
		<React.Fragment>
			<Row>
				<Col>
					<h2 className="text-center font-weight-bold text-uppercase">Vakf</h2>
					<h4 className="text-center"><strong>stajanje prilikom uenja</strong></h4>
					<hr />
				</Col>
			</Row>
			<Row>
				<Col>
					<br />
				</Col>
			</Row>
			<Row>
				<Col className="opisLekcije">
					Prilikom uenja Kur'ana, ua moe stati u sljedeim situacijama: ako je kraj ajeta, ako postoji
					znak za stajanje i kada mu ponestane daha. To ini na sljedei nain:
				</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<strong>1.</strong> Kada rije na kojoj staje zavrava <strong>kratkim vokalom</strong>  E{' '}
					<span className="arapski-lekcija "> </span> , I{' '}
					<span className="arapski-lekcija "> </span>, U{' '}
					<span className="arapski-lekcija "  >  </span>ili <strong>tenvinom</strong> IN{' '}
					<span className="arapski-lekcija ">  </span>, UN{' '}
					<span className="arapski-lekcija">  </span>, stat e kao da je na rijei <strong>sukun</strong>{' '}
					<span className="arapski-lekcija "> </span>, npr.:
				</Col>
			</Row>
			<Row className="text-center">
				<Col>{PlayerRow(data, 'row1')}</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<strong>2.</strong> Kada rije zavrava <strong>tenvinom</strong> EN{' '}
					<span className="arapski-lekcija "> </span>, stat e kao da je <strong>dugo</strong> A{' '}
					<span className="arapski-lekcija ">   </span>, npr.:
				</Col>
			</Row>
			<Row className="text-center">
				<Col>{PlayerRow(data, 'row2')}</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<strong>3.</strong> Kada rije zavrava <strong>dugim vokalom</strong> A{' '}
					<span className="arapski-lekcija">   </span>, <strong>dugim vokalom</strong> I{' '}
					<span className="arapski-lekcija"> </span> ili <strong>dugim vokalom</strong> U{' '}
					 <span className="arapski-lekcija"> </span>, stat e <u>bez ikakve promjene</u>, npr.:
				</Col>
			</Row>
			<Row className="text-center">
				<Col>{VjezbeRow(data, 'row3',"broj1")}{VjezbeRow(data, 'row3',"broj2")}{VjezbeRow(data, 'row3',"broj3")}{VjezbeRow(data, 'row3',"broj4")}{VjezbeRow(data, 'row3',"broj5")}</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<strong>4.</strong> Kada rije zavrava okruglim <strong>T</strong> <span className="arapski-lekcija"  >(/)</span>,
					stat e kao da je napisano slovo <strong>H </strong><span className="arapski-lekcija" >()</span>, bez obzira koji je
					vokal ili tenvin napisan na njemu, npr.:
				</Col>
			</Row>
			<Row className="text-center">
				<Col>{PlayerRow(data, 'row4')}</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<h3>Pregled u tabeli:</h3>
					<Table className="tabela-opis text-center" bordered hover responsive>
						<thead className="text-danger text-uppercase">
							<tr>
								<th>Kada rije zavrava na</th>
								<th>Stajemo</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									E <span class="arapski-lekcija "></span> , I{' '}
									<span className="arapski-lekcija "> </span> , U{' '}
									<span className="arapski-lekcija"> </span> , IN{' '}
									<span className="arapski-lekcija">  </span>, UN{' '}
									<span className="arapski-lekcija"> </span>
								</td>
								<td>
									kao da je sukun <span className="arapski-lekcija "> </span>
								</td>
							</tr>
							<tr>
								<td>
									EN <span className="arapski-lekcija "> </span>
								</td>
								<td>
									kao da je dugo A <span className="arapski-lekcija">  </span>
								</td>
							</tr>
							<tr>
								<td>
									dugo A   <span className="arapski-lekcija"> </span> , dugo I{' '}
									<span className="arapski lekcija"> </span> , dugo U{' '}
									 <span className="arapski-lekcija"> </span> 
								</td>
								<td>bez ikakve promjene</td>
							</tr>
							<tr>
								<td>
									okruglo T <span className="arapski-lekcija" > (/)</span>
								</td>
								<td>
									kao da je H <span className="arapski-lekcija" > ()</span>
								</td>
							</tr>
						</tbody>
					</Table>
				</Col>
			</Row>
			<Row>
				<Col className="opisLekcije my-3">
					<h3><strong>Znakovi za stajanje:</strong></h3>
					<br></br>
					<p>
						Iznad nekih rijei u Kur'anu nalaze se <strong>znakovi</strong> koji oznaavaju da li se na toj
						rijei mora stati ili se ne smije, da li je bolje stati ili je bolje prei. Ti znakovi su:
					</p>
					<br></br>
					<Table className="tabela-opis text-center" bordered hover responsive>
						<thead className="text-uppercase">
							<tr>
								<th className="text-danger">Mora stati</th>
								<th>Bolje stati</th>
								<th>Boje prei</th>
								<th className="text-danger">Ne smije se stati</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									<span className="arapski-lekcija"></span>
								</td>
								<td>
									<span className="arapski-lekcija">       </span>
								</td>
								<td>
									<span className="arapski-lekcija">       </span>
								</td>
								<td>
									<span className="arapski-lekcija"> </span>
								</td>
							</tr>
						</tbody>
					</Table>

					<br></br>

					<Table className="tabela-opis text-center" bordered hover responsive>
						<thead className="text-uppercase">
							<tr>
								<th>Sekta</th>
								<th>Tri takice</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									<span className="arapski-lekcija">   </span>
									<br />Pauzira se bez prekidanja daha.
								</td>
								<td>
									<span className="arapski-lekcija">   </span>
									<br />Ukoliko se pauzira na prvom znaku, onda je na drugom obavezno prei i obrnuto.
								</td>
							</tr>
						</tbody>
					</Table>

					<br></br>

					<p>
						<strong>NAPOMENA</strong>: Znak <span className="arapski-lekcija"> </span> ispod rijei je
						znak da se vokal ispod kojeg se nalazi ui <u>kratko</u>, a{' '}
						<span className="arapski-lekcija">  </span>je znak da se vokal ui <u>dugo</u>.
					</p>
				</Col>
			</Row>
			<Row>
				<Col>
					<br />
				</Col>
			</Row>
			<h2 className="text-center"><strong>VJEBA</strong></h2>
			<hr />
			<Row>
				<Col>
					<br />
				</Col>
			</Row>
			<Row className="text-center">
				<Col>
					{VjezbeRow(data, 'vjezba', 'red19')}
					{VjezbeRow(data, 'vjezba', 'red18')}
				</Col>
			</Row>
			<Row className="text-center">
				<Col>
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red21')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red20')}
				</Col>
			</Row>
			<Row className="text-center">
				<Col>
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red24')}
					{VjezbeRow(data, 'vjezba', 'red23')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red22')}
				</Col>
			</Row>
			<Row className="text-center">
				<Col>
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red26')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red25')}
				</Col>
			</Row>
			<Row className="text-center">
				<Col>
				<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red31')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red30')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red29')}
					<span className='tacka'></span>
					{VjezbeRow(data, 'vjezba', 'red28')}
				</Col>
			</Row>
	
			<Footer prev="/" next="/lekcija2" />
		</React.Fragment>
	);
}

export default L1;
